@import url(https://fonts.googleapis.com/css2?family=Piedra&family=Roboto:wght@300;400;500;700&display=swap);
*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}body{background-color:#000000}img{width:100%}a{text-decoration:none}.home{height:100vh;width:100%;max-width:768px;margin:0 auto;padding-top:8rem}.home__figure{margin-top:2rem}.home__nav{margin-top:5rem;display:flex;justify-content:center}.home__nav-text{padding-right:1rem}.home__nav-img{width:18px}.characters{padding:4rem 1rem 2rem 1rem;color:#000}@media (min-width: 768px){.characters{padding:4rem 2rem 2rem 2rem}}.characters .characters__container{width:100%;max-width:1200px;margin:0 auto}@media (min-width: 768px){.characters .characters__container{display:grid;grid-template-columns:repeat(2, 1fr);grid-column-gap:2rem}}@media (min-width: 1024px){.characters .characters__container{display:grid;grid-template-columns:repeat(3, 1fr);grid-column-gap:2rem}}@media (min-width: 1200px){.characters .characters__container{display:grid;grid-template-columns:repeat(4, 1fr);grid-column-gap:2rem}}.characters__card{margin-top:2rem;padding:1rem;border:1px solid #FDFA79;border-top-right-radius:5%;border-top-left-radius:5%;border-bottom-left-radius:1%;border-bottom-right-radius:1%}.characters__card-img{border-top-right-radius:5%;border-top-left-radius:5%}.characters__card-description{margin:-1rem auto 0 auto;padding:1rem;border-bottom-left-radius:1%;border-bottom-right-radius:1%;position:relative;z-index:10;text-align:center;background-color:#A0DCDC}.characters__card-description .description__grid{margin-top:0.75rem;display:grid;grid-template-columns:1fr 1fr;grid-row-gap:0.5rem}.characters__card-description .description__grid p{padding-bottom:0.25rem;border-bottom:1px solid #76FEFA}.title{text-align:center;font-size:3rem;font-family:"Piedra",cursive;color:#76FEFA}.btn{display:flex;align-items:center;margin:15px 15px 0;padding:.6em 1.1em;font-size:18px;font-size:1.125rem;text-decoration:none;outline:none;color:#000;background-color:#76FEFA;border-radius:3px;box-shadow:0 0 0 -2px #FDFA79,0 0 0 -1px #76FEFA;border:none;transition:box-shadow .3s}.btn:hover,.btn:focus{box-shadow:0 0 0 2px #FDFA79,0 0 0 4px #8dd5d5;transition-timing-function:cubic-bezier(0.6, 4, 0.3, 0.8);-webkit-animation:gelatine 0.5s 1;animation:gelatine 0.5s 1}.btn-secondary{background:#A0DCDC;box-shadow:0 0 0 -2px #FDFA79,0 0 0 -1px #A0DCDC}.btn-secondary:hover{box-shadow:0 0 0 2px #FDFA79,0 0 0 4px #8dd5d5}.btn:active,.btn-secondary:active{background:#76FEFA;transition-duration:0;box-shadow:0 0 0 2px #FDFA79,0 0 0 4px #5dfef9}@keyframes gelatine{from,to{transform:scale(1, 1)}25%{transform:scale(0.9, 1.1)}50%{transform:scale(1.1, 0.9)}75%{transform:scale(0.95, 1.05)}from,to{transform:scale(1, 1)}25%{transform:scale(0.9, 1.1)}50%{transform:scale(1.1, 0.9)}75%{transform:scale(0.95, 1.05)}}@-webkit-keyframes gelatine{from,to{transform:scale(1, 1)}25%{transform:scale(0.9, 1.1)}50%{transform:scale(1.1, 0.9)}75%{transform:scale(0.95, 1.05)}from,to{transform:scale(1, 1)}25%{transform:scale(0.9, 1.1)}50%{transform:scale(1.1, 0.9)}75%{transform:scale(0.95, 1.05)}}.navbar{width:100%;max-width:1200px;margin:1rem auto 0 auto;display:flex;justify-content:space-around;color:#76FEFA}

